<template>
  <div class="HomeIndex">
    <!-- 在此处使用引入的组件 -->
    <HeaderInformation></HeaderInformation>
    <div class="homeswiper">
      <el-carousel :interval="4000" arrow="always" height="700px">
        <el-carousel-item v-for="(item,index) in bannerlist" :key="index">
          <img :src="item.banner" class="carousel-img" />
        </el-carousel-item>
      </el-carousel>
    </div>

   <!-- 宣传信息 - 点击滑动展示 -->
   <div class="publicity-section">
  <button class="scroll-left" @click="scrollLeft">←</button>
  <div class="card-container">
    <div class="card" v-for="(item, index) in publicityItems" :key="index">
      <h4>{{ item.title }}</h4>
      <!-- <p>{{ item.description }}</p> -->
      <img :src="item.img" :alt="item.alt"  />
    </div>
  </div>
  <button class="scroll-right" @click="scrollRight">→</button>
</div>

    <!-- 动态渲染分类 -->
    <div v-for="(group, index) in classifiedNavbarList" :key="index"
     :class="(index % 2 === 0) ? 'HomeClassification' : 'HomeClassificationtwo'">
      <ul>
        <li v-for="item in group" :key="item.id"  @click="$router.push('/classify/' + item.tabCode)">
          <span>{{ item.tabName }}</span>
          <img :src="item.tabImageUrl" alt="">
        </li>
      </ul>
    </div>

    <!-- 引导购物网站 -->
    <div class="Guided">
    <div class="guided-container">
      <img class="guided-bg" :src="require('@/img/banner/banner1.png')" alt="background image">
      <div class="guided-content">
        <h2 class="guided-text">Discover Our Exclusive Products</h2>
        <button class="guided-btn" @click="redirectToSubsite">Shop Now</button>
      </div>
    </div>
  </div>

    <BottomInformation></BottomInformation>
  </div>
</template>

<script>
import BottomInformation from '@/components/BottomInformation.vue'
import HeaderInformation from '@/components/HeaderInformation.vue'
import { getnavabar, getbanner } from '@/api/home/home'
import { MainLinkClicks } from '@/api/hotspot/hotspot'
export default {
  name: 'HomeIndex',
  components: {
    BottomInformation,
    HeaderInformation
  },
  data () {
    return {
      imglist: [
        require('@/img/banner/banner1.png'),
        require('@/img/banner/banner2.png'),
        require('@/img/banner/banner3.png'),
        require('@/img/banner/banner4.png')
      ],
      navbarlist: [], // 导航栏列表
      bannerlist: [], // 轮播图列表
      publicityItems: [
        { title: 'High-end furniture customization', description: 'Compare all models.', img: 'http://minio.fohufo.com/fuhufo-bucket/default/2024-09-18-16-03-11/画板 1.png', alt: 'Custom Furniture' },
        { title: 'Globally renowned team', description: 'Compare all models.', img: 'http://minio.fohufo.com/fuhufo-bucket/default/2024-09-18-16-04-16/画板 2.png', alt: 'Custom Furniture' },
        { title: 'High-end custom style', description: 'Compare all models.', img: 'http://minio.fohufo.com/fuhufo-bucket/default/2024-09-18-16-04-53/画板 3.png', alt: 'Custom Furniture' },
        { title: 'Ingenuity and luxury customization', description: 'Compare all models.', img: 'http://minio.fohufo.com/fuhufo-bucket/default/2024-09-18-16-05-14/画板 4.png', alt: 'Custom Furniture' },
        { title: 'Exclusive home and private space', description: 'Compare all models.', img: 'http://minio.fohufo.com/fuhufo-bucket/default/2024-09-18-16-05-28/画板 5.png', alt: 'Custom Furniture' },
        { title: 'Extreme craftsmanship and individual design', description: 'Compare all models.', img: 'http://minio.fohufo.com/fuhufo-bucket/default/2024-09-18-16-05-41/画板 6.png', alt: 'Custom Furniture' },
        { title: 'Exquisite handicraft, high-end quality', description: 'Compare all models.', img: 'http://minio.fohufo.com/fuhufo-bucket/default/2024-09-18-16-06-27/画板 7.png', alt: 'Custom Furniture' },
        { title: 'Extraordinary taste, customized exclusivity', description: 'Compare all models.', img: 'http://minio.fohufo.com/fuhufo-bucket/default/2024-09-18-16-06-48/画板 8.png', alt: 'Custom Furniture' }
      ]
    }
  },
  created () {
    this.getnavabar()
    this.getbanner()
  },
  computed: {
    classifiedNavbarList () {
      const result = []
      const { length } = this.navbarlist

      for (let i = 0; i < length; i += 5) {
        result.push(this.navbarlist.slice(i, i + 3)) // 第一排3个
        if (i + 3 < length) {
          result.push(this.navbarlist.slice(i + 3, i + 5)) // 第二排2个
        }
      }

      return result
    }
  },
  methods: {
    // 获取导航栏列表
    async getnavabar () {
      const res = await getnavabar()
      // console.log(res)
      this.navbarlist = res.data
    },
    // 获取轮播图
    async getbanner () {
      const res = await getbanner()
      // console.log(res)
      this.bannerlist = res.data
    },
    scrollLeft () {
      const container = document.querySelector('.card-container')
      container.scrollBy({
        left: -300, // 调整为每次滚动的像素值
        behavior: 'smooth'
      })
    },
    scrollRight () {
      const container = document.querySelector('.card-container')
      container.scrollBy({
        left: 300, // 调整为每次滚动的像素值
        behavior: 'smooth'
      })
    },
    // 跳转到商品网站并记录
    async redirectToSubsite () {
      await MainLinkClicks()
      window.location.href = 'https://shop.fohufo.com/' // Replace with your subdomain link
    }
  }

}
</script>

