  color: white;
  text-align: center;
  border: none;
}

.item-list {
  display: flex;
  /* flex-wrap: wrap; */
  margin-top: 10px;
  gap: 20px; /* Increased gap between items */
  /* background-color: aquamarine; */
}
.item-list span{
  /* background-color: aquamarine; */
  /* width: 20px; */
  display: block;
  white-space: nowrap; /* 禁止换行 */
    overflow: hidden; /* 隐藏超出部分 */
    text-overflow: ellipsis; /* 用省略号替代超出部分 */
}

.item {
  width: 185px;
  height: 210px;
