  margin-bottom: 10px;
  white-space: wrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}

.drawer-product-stock {
  margin-left: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.drawer-product-stock h4 {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 5px;
}

.drawer-product-stock ul {
  list-style-type: none; /* 去掉默认的列表样式 */
  padding: 0; /* 去掉默认的内边距 */
}

.drawer-product-stock li {
  margin: 5px 0; /* 每个库存项之间的间距 */
  font-size: 14px; /* 字体大小 */
  color: #000000; /* 字体颜色 */
}

.drawer-product-description {
  margin-left: 10px;
  margin-top: 20px;
  font-size: 14px;
  line-height: 1.5;
}

.drawer-buttons {
  display: flex;
  margin-left: 10px;
  margin-top: 20px;
  gap: 10px;
}

.bottom-info {
  text-align: center;
  margin-top: 20px;
  font-size: 18px;
  font-weight: bold;
  color: #333;
}

@media (max-width: 768px) {
  .pagination-controls {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.pagination-controls button {
  padding: 10px 20px;
  width: 150px;
