}

.pagination-controls button:disabled {
  background-color: gray;
  cursor: not-allowed;
}

.pagination-controls span {
  align-self: center;
  font-size: 16px;
  margin: 0 10px;
}
  .container {
  margin-top: 20px;
  height: 13vh;
  /* background-color: aqua; */
}
  .detailesbtn1 {
    width: 150px;
    font-size: 16px;
    margin-left: 40px;
    height: 40px;
    border-radius: 10px;
    background-color: black;
    color: white;
    text-align: center;
    border: none;
  }

  .item-list {
    display: flex;
    flex-wrap: wrap;
    margin-top: 10px;
    font-size: 16px;
  }

  .detailesbtn {
    margin-left: 30px;
    width: 200px; /* Adjusted width for mobile */
    height: 40px; /* Adjusted height for mobile */
    font-size: 14px; /* Adjusted font size for mobile */
  }

  .drawer-buttons button {
    width: 100px; /* Adjusted width for mobile */
    height: 30px; /* Adjusted height for mobile */
    font-size: 14px; /* Adjusted font size for mobile */
  }

  .item {
    width: 220px; /* Adjust the item width for smaller screens */
